<template>
  <div>
    <mrc_ess_table :headers='headers'
                   v-if="$route.name == 'role'"
                   pageName='addRole'
                    :hideAddBtn="!hasBtn('add-role')"
                   :paginationOptions='paginationOptions'
                   @turnPage='getList'
                   :isLoading='loading'
                   :slotKey="['name','role_name','is_used']"
                   :items='desserts'>

      <template v-slot:item.name='{item}'>
        <div style="width: 400px;white-space: nowrap;;overflow: hidden;"
             class="d-flex align-center">
          <div style="width: 90%;white-space: nowrap;;overflow: hidden;">
            <v-chip v-for="(items,index) in item.name"
                    v-show="item.name.length > 0"
                    :key='index'
                    style="font-size: 12px;height:28px">{{items}}</v-chip>
          </div>
          <!-- <span>（others）</span> -->
        </div>
        <!-- <template v-for="(name,i) in item.name">
          <v-chip :key="i">{{name}}</v-chip>
        </template> -->
      </template>
      <template v-slot:item.role_name='{item}'>
        <v-tooltip bottom
                   max-width='400'>
          <template v-slot:activator="{ on, attrs }">
            <div class="role_name"
                 v-bind="attrs"
                 v-on="on">
              <span class="name">{{INT(item.role_name)}}</span>
              <span class="desc">{{INT(item.role_description)}}</span>
            </div>
          </template>
          <span>{{INT(item.role_description)}}</span>
        </v-tooltip>

      </template>

      <template v-slot:item.actions='{item}'>
        <menu-list :list='list'
                   @handleClick='handleClick($event,item)'>
          <template v-slot:showAction>
            <v-icon v-btnAuth="['view-role','edit-role','delete-role']">mdi-dots-vertical</v-icon>
          </template>
        </menu-list>
      </template>
      <template v-slot:item.is_used="{ item }">
        <span class="status"
              :class="{'is_used': !item.is_used}">{{item.is_used ? $t('label_To_enable_the') : $t('label_disable')}}</span>
      </template>
    </mrc_ess_table>
    <router-view v-else></router-view>
  </div>

</template>

<script>
import { role_list, role_del } from '@/service/api/role'
import { getBusinessList } from '@/service/api/prompt'
export default {
  components: {},
  props: {},
  data () {
    return {
      paginationOptions: {
        pagesize: this.$store.state.pageSize,
        cpage: this.$store.state.cpage,
        totalCount: 0, //总条数
        totalpage: 0, //总页数
      },
      loading: false,
      desserts: [],

    };
  },
  created () { },
  async mounted () {
    if (this.$route.name == 'role') {
      await this.getList()
    }

  },
  beforeDestroy () {
    this.$store.commit('setCpage', 1)
  },
  methods: {
    /**
       * @description: 获取角色列表
       * @param {type} 
       * @return: 
       * @author: lwq
       */
    async getList (obj) {
      if (obj) {
        this.paginationOptions = obj
      }
      this.loading = true
      const list = await role_list({
        cpage: this.paginationOptions.cpage,
        pagesize: this.paginationOptions.pagesize,
        org_level_uuid: this.$store.state.currentCompany
      })
      if (list.code == 200) {
        list.data.forEach(item => {
          if (item.name)
            item.name = item.name.indexOf(',') != -1 ? item.name.split(',') : [item.name];
          else
            item.name = []
        })
        this.desserts = list.data
        this.paginationOptions.totalCount = list.totalCount
        this.paginationOptions.totalpage = list.totalpage
      }
      this.loading = false
    },
    /**
     * @description: 进入详情
     * @param {index} 点的第几个menu 
     * @param {item} 点的整条数据
     * @return: 
     * @author: lwq
     */
    handleClick ({ item, index }, obj) {
      if (item.type == 'view') {
        this.$store.commit('set_subPage_name', obj.role_name)
        this.$store.commit('set_uuid', obj.uuid)
        this.$store.commit('set_iconstatus', true)
        this.$router.push({
          name: 'roleInfo',
          params: {
            name: obj.role_name,
            uuid: obj.uuid
          }
        })
      }
      if (item.type == 'edit') {
        this.$store.commit('set_subPage_name', obj.role_name)
        this.$store.commit('set_uuid', obj.uuid)
        this.$store.commit('set_iconstatus', false)
        this.$router.push({
          name: 'roleInfo',
          params: {
            name: obj.role_name,
            uuid: obj.uuid
          }
        })
      }
      if (item.type == 'del') {
        this.$myConfirm({ content: this.$t('message_Confirm_the_deletion') })
          .then(async () => {
            const del = await role_del({
              uuid: obj.uuid
            })
            if (del.code == 200) {
              this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
              // this.desserts.length === 1  只有一条数据 删除就请求上一页的数据
              if (this.desserts.length === 1 && this.paginationOptions.cpage > 1) {
                this.paginationOptions.cpage--
              }
              await this.getList()
            }
          }).catch(() => { })
      }
    },
    /**
     * @description: 去添加页面
     * @param {type} 
     * @return: 
     * @author: lwq
     */
    handleAdd () {
      this.$router.push({
        name: 'addRole'
      })
    }
  },
  computed: {
    list () {
      return [
        { name: this.$t('label_View'), show: this.hasBtn('view-role'), type: 'view' },
        { name: this.$t('label_Edit'), show: this.hasBtn('edit-role'), type: 'edit' },
        { name: this.$t('label_delete'), show: this.hasBtn('delete-role'), type: 'del' }
      ]
    },
    headers () {
      return [
        {
          text: this.$t('label_role'),
          align: 'start',
          sortable: false,
          value: 'role_name',
          width: 100
        },
        {
          text: this.$t('label_Use_the_unit'),
          align: 'start',
          sortable: false,
          value: 'name',
          width: 400
        },
        {
          text: this.$t('label_use_people'),
          align: 'start',
          sortable: false,
          value: 'userNum',
        },
        {
          text: this.$t('label_state'),
          align: 'start',
          sortable: false,
          value: 'is_used',
        },
        {
          text: ' ',
          align: 'start',
          sortable: false,
          value: 'actions'
        }
      ]
    }
  },
  watch: {
    async '$route' (val) {
      if (val.name == 'role') {
        await this.getList()
        this.$store.commit('setTab', 0)
      }
    }
  }
};
</script>

<style scoped>
.role_name {
  display: flex;
  flex-direction: column;
  padding: 10px 0;
}
.name {
  font-size: 16px;
}
.desc {
  width: 160px;
  color: #afabab;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.org_level_name {
  overflow: hidden;
  padding: 10px;
  white-space: nowrap;
  max-width: 30vw;
}
</style>
